/**
 * App Component
 * 
 * This is the main component of the application.
 * It uses the AssistantUI component to provide a modern interface
 * for interacting with the voice assistant.
 */

// Import React and hooks
import React, { useEffect } from 'react';

// Import services context
import { useServices } from './context/services-context';

// Import AssistantUI component
import AssistantUI from './components/AssistantUI';

// Import styles
import './App.css';

const App = () => {
  // Get services from context
  const {
    configService,
    assistantService,
    emotionService,
    apiService,
    voiceService,
    memoryService,
    themeService
  } = useServices();

  // Initialize services when component mounts
  useEffect(() => {
    // Initialize all services
    configService?.initialize?.();
    assistantService?.initialize?.();
    emotionService?.initialize?.();
    apiService?.initialize?.();
    voiceService?.initialize?.();
    memoryService?.initialize?.();
    themeService?.initialize?.();

    // Apply theme from theme service
    if (themeService) {
      themeService.applyCurrentTheme();
    }

    // Log initialization
    console.log('All services initialized');
  }, [
    configService,
    assistantService,
    emotionService,
    apiService,
    voiceService,
    memoryService,
    themeService
  ]);

  return (
    <div className="app-container" dir="rtl">
      {/* Render the AssistantUI component */}
      <AssistantUI />
    </div>
  );
};

export default App;
